import uniLoadMore from '@/colorui/components/uni-load-more.vue';
import misEnum from '../../common/mis-enum.js';
export default {
	components: {
		uniLoadMore
	},
	onLoad(option) {
		this.page = 1;
		this.records = [];
		this.loadData();
	},
	data() {
		return {
			page: 1,
			pageSize: 10,
			status: 'more',
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			records: []
		}
	},
	onReachBottom() {
		if (this.status !== "noMore") {
			this.status = 'more';
			this.loadData();
		}
	},
	methods: {
		loadData() {
			this.status = 'loading';
			global.$http.post('/dining/lead/list', {
				params: {
					status: this.recordStatus,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					if (res.data.totlePage <= this.page) {
						this.status = 'noMore';
					} else {
						this.status = "more";
					}
					if (this.page === 1) {
						this.records = res.data.list;
					} else {
						res.data.list.forEach(c => {
							this.records.push(c);
						});
					}
					this.page++;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getStatusStr(status) {
			return misEnum.LeaderBookFoodEnumMap.get(status);
		},
		recordDetail: function(record) {
			uni.navigateTo({
				url: "../work/leaderBookFoodDetail?para=" + encodeURIComponent(JSON.stringify(record))
			});
		}
	}
}
